import React, { Component } from 'react';
import {
  Button,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
  Col,
  Alert,
  Form,
  FormGroup,
  Input,
  Label,
  Row,
} from 'reactstrap';

class Forms extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.toggleFade = this.toggleFade.bind(this);
    this.state = {
      collapse: true,
      fadeIn: true,
      timeout: 300,
      date: '',
      hour: '',
      client: '',
      phone: '',
      anotation: '',
      type: 0,
      address: '',
      sector: 0,
      ok: false,
    };
  }

  toggle() {
    this.setState({ collapse: !this.state.collapse });
  }

  toggleFade() {
    this.setState((prevState) => { return { fadeIn: !prevState }});
  }

  valida = (campo) => {
    return (valor) => {
      this.setState({
        [campo]: valor.target.value,
      })
    }
  }

  login=(e)=>{
    e.preventDefault();
    console.log(this.state)
    const {address, sector, phone, type, anotation, date, hour, } = this.state;
    const id = localStorage.getItem('uid');
        return fetch('https://peerpeel.herokuapp.com/v1/service',{
            method: 'POST',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                address,
                sector,
                phone,
                type,
                anotation,
                date,
                hour,
                client: id
            })
        })
        .then(res => res.json())
        .then(json =>{
          console.log(json);
          this.setState({
            address: '',
            sector: 0,
            type: 0,
            phone: '',
            anotation: '',
            date: '',
            hour: ''
          });
          alert('servicio creado pronto nos comunicaremos contigo');
          return console.log(this.state);
        })
        .catch(err => this.setState({error:true}));
      }

  render() {
    console.log(this.state)
    return (
      <div className="animated fadeIn">

        <Row>
          <Col xs="12" md="12">
            <Card>
              <CardHeader>
                <strong>Service</strong>
              </CardHeader>
              <CardBody>
                <Form action="" method="post" encType="multipart/form-data" className="form-horizontal">
                  <FormGroup row>
                    <Col md="3">
                      <Label htmlFor="text-input">Direccion</Label>
                    </Col>
                    <Col xs="12" md="9">
                      <Input type="text"  id="text-input" name="text-input" placeholder="Text" value={this.state.address} onChange={this.valida('address')} />
                    </Col>
                  </FormGroup>
                  <FormGroup row>
                    <Col md="3">
                      <Label htmlFor="phone-input">telefono de contacto</Label>
                    </Col>
                    <Col xs="12" md="9">
                      <Input type="number" id="phone-input" name="phone-input" placeholder="Enter phone" value={this.state.phone}  onChange={this.valida('phone')} autoComplete="phone"/>
                    </Col>
                  </FormGroup>

                  <FormGroup row>
                    <Col md="3">
                      <Label htmlFor="date-input">Fecha del Servicio</Label>
                    </Col>
                    <Col xs="12" md="9">
                      <Input type="date" id="date-input" name="date-input" placeholder="date" value={this.state.date}  onChange={this.valida('date')} />
                    </Col>
                  </FormGroup>
                  <FormGroup row>
                    <Col md="3">
                      <Label htmlFor="hour-input">Hora del servicio</Label>
                    </Col>
                    <Col xs="12" md="9">
                      <Input type="text" id="hour-input" name="hour-input" placeholder="Enter hour" autoComplete="hour" value={this.state.hour}  onChange={this.valida('hour')}/>
                    </Col>
                  </FormGroup>
                  <FormGroup row>
                    <Col md="3">
                      <Label htmlFor="textarea-input">Anotaciones a tener en cuenta</Label>
                    </Col>
                    <Col xs="12" md="9">
                      <Input type="textarea" name="textarea-input" id="textarea-input" rows="9"
                             placeholder="Content..." value={this.state.anotation} onChange={this.valida('anotation')} />
                    </Col>
                  </FormGroup>
                  <FormGroup row>
                    <Col md="3">
                      <Label htmlFor="select">Sector</Label>
                    </Col>
                    <Col xs="12" md="9">
                      <Input type="select" name="select" id="select" value={this.state.sector} onChange={this.valida('sector')}>
                        <option value="0">Seleccione un sector</option>
                        <option value="Sur">Sur</option>
                        <option value="Norte">Norte</option>
                        <option value="Este">Este</option>
                        <option value="Oeste">Oeste</option>
                      </Input>
                    </Col>
                  </FormGroup>
                  <FormGroup row>
                    <Col md="3">
                      <Label htmlFor="type">Tipo de servicio</Label>
                    </Col>
                    <Col xs="12" md="9">
                      <Input type="select" name="type" id="type" value={this.state.type} onChange={this.valida('type')}>
                        <option value="0">Seleccione un tipo</option>
                        <option value="Standard">Stadard 53.000</option>
                        <option value="Cooper">Cooper 63.000</option>
                        <option value="Silver">Silver 73.000</option>
                      </Input>
                    </Col>
                  </FormGroup>
                </Form>
              </CardBody>
              <CardFooter>
                <Button type="submit" size="sm" color="primary" onClick={(e)=>this.login(e)}>
                <i className="fa fa-dot-circle-o"></i> Submit</Button>
                <Button type="reset" size="sm" color="danger"><i className="fa fa-ban"></i> Reset</Button>
              </CardFooter>
            </Card>
          </Col>
        </Row>


      </div>
    );
  }
}

export default Forms;
